<script>
import recson from '../components/Home-recon.vue';

export default {
	data() {
		return {
			check: false,
			totalsprices:""
		};
	},
	computed: {
		gooddata() {
			return this.$store.state.goodsdata;
		},
		isselectlist(){
			return this.$store.state.goodsdata.every(value=>value.checkgoods);
		// if(this.$store.state.goodsdata[1].checkgoods==true){
		// 	this.check=true
		// }
		
	},
	getTotal(){
		var num = 0;
		 this.$store.state.goodsdata.forEach((value) => {
			if(value.checkgoods){
				num+=value.clicknum*value.goodsprice
			}
		})
		return num;
	}
		
	},
	methods: {
		Fh() {
			this.$router.go(-1);
		},
		goodscar() {
			this.$router.push('/Goods');
		},
		Checkout(nums) {
			console.log(this.$store.state.goodsdata,nums);
			this.$router.push('/Checkout');
			
			// if (this.$store.state.goodsdata[nums].checkgoods==true) {
				
			// } 
			// this.$store.state.goodsdata.filter(function(a,b,c){
			// 	this.$store.state.Checkoutlist.push()
			// })
		},
		QXz(num) {
			window.console.log(num),
			this.check=!this.check
			this.$store.state.goodsdata.forEach((nums)=>{nums.checkgoods=this.check});
			
			console.log(this.check)
		},
		onClose(clickPosition, instance) {
			switch (clickPosition) {
				case 'left':
				case 'cell':
				case 'outside':
					instance.close();
					break;
				case 'right':
					// Dialog.confirm({
					//   message: '确定删除吗？'
					// }).then(() => {
					//   instance.close();
					// });
					break;
			}
		},
		carnum(nuw) {
				// var thise=this;
				// window.console.log(thise.$store.state.goodsdata.clicknum)
		        this.$prompt('输入数量', '修改数量', {
		          confirmButtonText: '确定',
		          cancelButtonText: '取消',
		          inputPattern: /^[0-9]*$/,
		          inputErrorMessage: '数量格式错误'
		        }).then(({ value }) => {
					console.log(this.$store.state.goodsdata[nuw])
					this.$store.state.goodsdata[nuw].clicknum=value
		          this.$message({
		            type: 'info',
		            message: '数量修改为:' + value,
					duration:500,
		          });
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: '已取消输入'
		          });       
		        });
		      },
		del(number){
			this.$store.state.goodsdata.splice(number,1)
			console.log(this.$store.state.goodsdata)
			if(this.$store.state.goodsdata==[]){
				this.$store.state.datacount=false
			}
		},
	},
	watch: {
		'$route.path': function(newValue, oldValue) {
			// if(oldValue==)

			window.console.log(newValue);
			window.console.log(oldValue);
		}
	},
	components: {
		recson
	}
};
</script>

