#check[type='checkbox'] {
	width: 16px;
	height: 16px;
	text-align: center;
}
#check[type='checkbox']:after {
	width: 100%;
	height: 100%;
	top: 0px;
	content: ' ';
	background-color: #7f0019;
	color: White;
	display: inline-block;
	visibility: visible;
	border-radius: 50%;
	border: 1px solid black;
}
#check[type='checkbox'][disabled='disabled']:after {
	width: 100%;
	height: 100%;
	top: 0;
	content: ' ';
	background-color: #7e0018;
	color: white;
	display: inline-block;
	visibility: visible;
	border-radius: 2px;
	border: 1px solid #a9a9a9;
}
#check[type='checkbox']:checked:after {
	content: '\2713'; /*UNICODE中对号*/
	font-size: 12px;
	font-weight: bold;
}
