<template>
	<div>
		<header>
			<van-nav-bar title="订单结算" left-arrow @click-left="onClickLeftcheckout"><!-- <van-icon name="shopping-cart-o" slot="right" /> --></van-nav-bar>
		</header>
		<div class="page__content">
			<div class="section-address" @click="address">还没有填写地址，快去添加吧</div>

			<goodslist></goodslist>
			<!-- -=-=-=-=-=-=-==-=--==--=上面 是 商品 以及数量 -->
			<!-- -=-=-=-=-=-=-==-=--==--=上面 是 商品 以及数量 -->
			<!-- -=-=-=-=-=-=-==-=--==--=上面 是 商品 以及数量 -->
			<!-- -=-=-=-=-=-=-==-=--==--=下面 是 支付方式等 -->
			<!-- -=-=-=-=-=-=-==-=--==--=下面 是 支付方式等 -->
			<div class="PageCheckoutPartTotal">
				<div class="section-delivery">
					<div class="section-label">配送方式</div>
					<div class="section-content">
						<div class="content-line">
							<div class="content-line-label">快递</div>
							<div class="content-line-value">根据收货地区自动分配</div>
						</div>
						<div class="content-line">
							<div class="content-line-label">运费</div>
							<div class="content-line-value">￥0</div>
						</div>
					</div>
				</div>
				<!-- =--=-=-=-=-=-=-=-=-=-=-=支付方式=--=-=-=-= -->
				<!-- =--=-=-=-=-=-=-=-=-=-=-=支付方式=--=-=-=-= -->
				<!-- =--=-=-=-=-=-=-=-=-=-=-=支付方式=--=-=-=-= -->
				<div class="section-payment" @click="drawer = true" type="primary">
					<div class="section-label">支付方式</div>
					<div class="section-content">
						{{this.$store.paydata}}
						<van-icon name="arrow" />
					</div>
				</div>
			</div>
		</div>

		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<!-- =-=--=-=-=-=-=-=-=-=-=-=支付  0=-=--= -->
		<div class="footbar-wrap-car">
			<div class="PageCartPartActions">
				<div class="part-actions-meta ">
					<div class="part-actions-quantity clearfix">
						<!-- 						<div class="checkbox-wrap">
							<span>✔</span>全选
						</div> -->
						<div>2件商品</div>
					</div>
					<div class="part-actions-total">
						合计
						<span>￥8.00</span>
					</div>
				</div>
				<div class="part-actions-quantity"><button type="button" class="mu-button" @click="Checkout">订单提交</button></div>
			</div>
		</div>
		<!-- ==--=-=-=-=-=-=-=支付详情+__+_+_+_+_+_+_+_+ -->
		<!-- ==--=-=-=-=-=-=-=支付详情+__+_+_+_+_+_+_+_+ -->
		<!-- ==--=-=-=-=-=-=-=支付详情+__+_+_+_+_+_+_+_+ -->
		<el-drawer title="选择您的支付方式" :visible.sync="drawer" :direction="direction" :before-close="handleClose" size="50%">
			<!-- <span>我来啦!</span> -->

			<ul v-for="(item, i) in paylist" :key="i">
				<br />
				<li>
					<h2 style="text-align: center;" @click="cli(item.name)">
						<van-icon :name="item.icon" />
						{{ item.name }}
					</h2>
					<hr />
				</li>
			</ul>
		</el-drawer>
	</div>
</template>

<script>
import goodslist from '../components/Checkout/goodslist.vue';
export default {
	data() {
		return {
			drawer: false,
			direction: 'btt',
			paylist: [
				{
					icon: 'wechat',
					name: 'Wechat'
				},
				{
					icon: 'alipay',
					name: 'Alipay'
				}
			]
		};
	},
	methods: {
		onClickLeftcheckout() {
			this.$router.go(-1);
		},
		Checkout() {
			this.$router.push('/orders');
		},
		handleClose(done) {
			this.$confirm('确认关闭选择？')
				.then(() => {
					done(close());
				})
				.catch(() => {});
		},
		address() {
			this.$router.push('/address');
		},
		cli(index) {
			this.drawer = false;
			if (index=="Alipay") {
				this.$store.paydata="支付宝"
			} else{
				this.$store.paydata="微信支付"
			}
			// this.$store.paydata=index
			console.log(this.$store.paydata)
		}
	},
	components: {
		goodslist
	}
};
</script>

