<template>
	<div>
		<div class="part-recommendations-wraps">
			<div class="wrap">
				<div class="MuCommodityList clearfix">
					<ul class="clearfix">
						<li @click="Goods" v-for="(item,i) in totalPrice" :key="i">
							<img :src="item.imgsrc" >
							<p> {{item.reconname}} </p>
							<h6>￥{{item.reconsprice}}</h6>
						</li>
					</ul>
					<van-list
					  v-model="loading"
					  :finished="finished"
					  finished-text="已加载完成"
					  @load="onLoad"
					>
					<!-- -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-= -->
					<ul class="clearfix">
						<li @click="Goods" v-for="(item,i) in list" :key="i">
							<img :src="item.imgsrc" >
							<p> {{item.reconname}} </p>
							<h6>￥{{item.reconsprice}}</h6>
						</li>
					</ul>
					<!-- -=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-= -->
					  <!-- <van-cell
					    v-for="item in list"
					    :key="item"
					    :title="item"
					  /> -->
					</van-list>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		computed:{
			totalPrice (){
				return this.$store.state.recongoodslist
			}
		},
		data() {
			return {
				list: [],
				loading: false,
				finished: false,
				nums:1
			}
		},
		methods:{
			search(){
				this.$router.push("/Search")
			},
			Category(){
				this.$router.push("/Category")
			},
			Notifications(){
				this.$router.push("/Notifications")
			},
			Cars(){
				this.$router.push("/cars")
			},
			My(){
				this.$router.push("/My")
			},
			Goods(){
				this.$router.push("/Goods")
			},
			Cat(){
				this.$router.push("/Cat")
			},
			onLoad() {
			      // 异步更新数据
				 
			      setTimeout(() => {
					  let onLoaddata= {
						  	imgsrc:require("../assets/sp3.jpg"),
						  	reconname:"扫除用品系列_地毯清洁纸",
						  	reconsprice:24
						}
							 
					  
			        for (let i = 0; i < 10; i++) {
			          this.list.push(onLoaddata);
			        }
					this.nums++;
			        // 加载状态结束
			        this.loading = false;
					window.console.log()
			        // 数据全部加载完成
			        if (this.nums>= 6) {
			          this.finished = true;
			        }
			      },700);
			    }
			
		}
	}
</script>

<style>
		.MuCommodityList{
			padding-top: 10px;
		}
	 .part-recommendations-wraps .MuCommodityList ul li{
		background-color: white;
		padding-bottom: 10px;
		width: 50%;
		-webkit-box-flex: 0;
		flex-grow: 0;
		flex-shrink: 0;
		padding-left: 2.5px;
		padding-right: 2.5px;
		float: left;text-indent: 0.62em;

	}
		 .part-recommendations-wraps .MuCommodityList ul li img{
			width:initial;
			display: block;
			opacity: 1;
			display: block;
			max-width: 100%;
			    width: auto;
		}
				 .part-recommendations-wraps .MuCommodityList ul li p{
					    margin-top: 9px;
						font-size: 13px;overflow:hidden;
						white-space:nowrap;
						text-overflow: ellipsis;
						color: #030303;width: 100%;
						line-height: 18px;
						letter-spacing: -.15px;    margin-top: 5px;
				}
								 .part-recommendations-wraps .MuCommodityList ul li h6{
						margin-top: 5px;
						font-size: 15px;
						line-height: 21px;
						color: #000;
						letter-spacing: -.17px;
						font-weight: 900;
								}
</style>