<template>
    <div>
          <!-- Swiper -->
        <div class="swiper-container">
            <div class="swiper-wrapper swiper-wrapper-bootpage">
                <div class="swiper-slide">
                    <img class="animated zoomIn" v-show="flag" src="../assets/bootpage01.jpg" alt="">
					
					<h3 class="animated fadeIn delay-1s ">自然、當然、無印</h3>
                </div>
                <div class="swiper-slide">
                    <img class="animated bounceIn" v-show="flag" src="../assets/bootpage03.jpg">
					<!-- <h2 class="animated fadeInLeft delay-2s">纯朴、简洁、环保、以人为本</h2> -->
					<h3 class="animated fadeInLeft delay-3s">简约而不简单</h3>
                </div>
                <div class="swiper-slide">
                    <img class="animated zoomInDown muji-logo" v-show="flag" src="../assets/muji-logo.png" alt="">
                    <button @click="go" class="animated fadeInUp delay-0.5s" v-show="flag">立即体验</button>
                </div>
            </div>
            <!-- Add Pagination -->
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import Swiper from "swiper";
    export default {
        data() {
            return {
                flag:true
            }
        },
        methods: {
            go() {
                localStorage.one = true;
                this.$router.push("/Home");
            }
        },
        created() {
            // 判断是否执行引导页
            if(localStorage.one) {
                this.$router.push("/Banner");
            }else{
                localStorage.one = true;
            }
        },
        mounted() {
            let _this = this;
            new Swiper('.swiper-container', {
                pagination: {
                    el: '.swiper-pagination',
                },
                on: {
                    slideChangeTransitionStart: function(){
                        _this.flag = false;
                    },
                    slideChangeTransitionEnd: function(){
                        _this.flag = true;
                        // alert(this.activeIndex);//切换结束时，告诉我现在是第几个slide
                    },
                },
            });
        },      
    }
</script>

<style lang="scss" scoped>
   .swiper-container {
       position:absolute;
       top:0px;
       left: 0px;
	   right: 0;
		bottom: 0;
    }
    .swiper-wrapper-bootpage .swiper-slide {
      text-align: center;
      display: block;
	  width: 100%;
	  height: 100%;
	position: relative;
    }
	
	.swiper-wrapper-bootpage .swiper-slide img{
		width: 90%;
		height: 80%;
		margin: 10px auto;
	}
	.swiper-wrapper-bootpage .swiper-slide .muji-logo{
		width:70%;
		height: auto;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		margin: auto;
	}
	.swiper-wrapper-bootpage .swiper-slide:nth-child(2) h3{
		position: absolute;
		left: 90px;bottom: 65px;
		font-size: 20px;
		color: #B79474;
		font-size:30px;
		letter-spacing: 1.2px;
	}
	.swiper-wrapper-bootpage .swiper-slide:nth-child(1) h3{
		color: white;color: #A1A1A1;
		position: absolute;
		bottom: 70px;
		left: 90px;
		letter-spacing: 0.7px;
		font-family: "zapf dingbats"	
     }
    button{
        width: 200px;
        height: 45px;
        line-height: 45px;
        border: 1px solid #C8EFD4;
        background: #80011A;
        position: absolute;
        left:0px;
        right: 0px;
        bottom: 70px;
        margin:0 auto;
		border-radius: 3px;
		color: white;
		font-weight: bold;
		letter-spacing: 2.7px;
		font-size: 20px;
    }
</style>
