<template>
    <div>
        <div class="banner">
        	<div class="content">
        		<img src="../assets/BANN.jpg" >
        	</div>
        	<h2>這個廣告只會打上三秒，<br>不多不少剛剛好。</h2>
        	<button type="button" @click="btn" class="btn btn-info">點此跳過( {{num}} )</button>
        </div>
    </div>
</template>

<script>
    export default {    
        data() {
            return {
                num:3,
                cleardata:null
            }
        },
		methods:{
			btn(){
				this.$router.push("/Home")
			}
		},
        created() {
            this.cleardata = setInterval(()=>{
                if(this.num == 0){
                    this.$router.push("/home");
                }else{
                    this.num--
                }
            },1000)
        },
        beforeDestroy() {
            //  页面跳转先销毁在跳转
            clearInterval(this.cleardata)
        },
    }
</script>

<style lang="scss" scoped>
html,body,p,h1,h2,h3,h4,h5,h6{
	margin: 0;
	padding: 0;
}
.banner button{
	position: absolute;
	right: 17px;
	top:25px;
	border-radius: 3px;
	background-color: #F6F3EE;
	color:#916238;
	border: 1px solid #9C6D43;
}
.banner button:hover{
		background-color: rgba(0,0,0,0.4);
}
.content{
	    width: 100%;
	    height: 100%;
	    top:0;
	    z-index: -1; 
	    position: absolute;
}
img{
    display: block;
    outline: none;
    border:0;
    height: 100%;
    width: 100%;
}
h2{
	position: absolute;
	top: 40px;
	left: 20px;
	font-size:20px;line-height: 25px;
	color: #895830;
}
</style>